<template>
  <div class="weather-wapper">
    <mt-header title="寒潮预警">
      <router-link to="/home/weather" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="warning-wrapper">
      <borderTitle :title="title1"></borderTitle>
      <div class="warning-info">
        <img src="../../assets/imgs/blue_warning.png"/>
        <div>
          <p class="text-h bottom">
            <span class="colorBlue">寒潮预警</span>
            <span class="Fright">{{waring}}</span>
          </p>
          <p class="text-h">
            <span class="colorBlue">预警发布时间</span>
            <span class="Fright">{{publishTime}}</span>
          </p>
        </div>
      </div>
      <div class="cold-info">
        <borderTitle :title="title2"></borderTitle>
        <div class="cold-content">
          <p>48小时内最低气温将要下降8℃以上，最低气温小于等于4℃，陆地平均风力可达5级以上；并可能持续。</p>
        </div>
      </div>
      <div class="emergency-guide">
        <borderTitle :title="title3"></borderTitle>
        <ul class="list">
          <li v-for="item in items">{{item}}</li>
        </ul>
      </div>
    </div>

  </div>
</template>

<script>
  import borderTitle from '../../components/borderTitle.vue'

  export default {
    name: "weather",
    data() {
      return {
        title1: "预警信息",
        title2: "寒潮预警",
        title3: "应急指南",
        waring: "蓝色预警",
        publishTime: "2017年11月15日  16:32:25",
        items: [
          '1、政府及有关部门按照职责做好防寒潮准备工作；',
          '2、注意添衣保暖；',
          '3、对热带作物、水产品采取一定的防护措施；',
          '4、做好防风准备工作。'
        ]
      }
    },
    components: {
      borderTitle
    }
  }
</script>

<style lang="scss" scoped>
  .weather-wapper {
    /*margin-top:1.2rem;*/
    padding-top: 1.2rem;
    color: #fff;
  }

  .warning-wrapper {
    width: 690px;
    padding-top: 30px;
    margin: 0 auto;
    .warning-info {
      margin: 30px 0 0;
      display: flex;
      padding-bottom: 48px;
      border-bottom: 1px solid #29374a;
      img {
        width: 122px;
        height: 102px;
        margin-right: 52px;
      }
      .text-h {
        height: 44px;
        line-height: 44px;
        font-size: 26px;
        .colorBlue {
          color: #bffffb;
          display: inline-block;
          width: 190px;
        }
      }
      .bottom {
        margin-bottom: 18px;
      }
    }

    .cold-info {
      margin: 30px 0 0;
      padding-bottom: 46px;
      border-bottom: 1px solid #29374a;
      .cold-content {
        margin-top: 29px;
        p {
          line-height: 40px;
          font-size: 26px;
        }
      }
    }

    .emergency-guide {
      margin-top: 30px;
      .list {
        margin-top: 30px;
        li {
          line-height: 40px;
          font-size: 26px;
        }
      }
    }
  }
</style>


