<template lang="html">
    <div class="setUp-wrap">
       <mt-header title="设置">
          <mt-button icon="back" slot="left" @click="back"></mt-button>
       </mt-header>
       <div class="content-wrap">
	    	<mt-cell class="cell-wrap" title="字号" :to="{path:'/mine/fontSize'}" is-link></mt-cell>
	    	<mt-cell class="cell-wrap" title="清空数据"  value="10.2M"></mt-cell>
	    	<mt-cell class="cell-wrap last" title="意见反馈"  is-link></mt-cell>
       </div>
    </div>
</template>
<script>
export default {
  methods:{
    back(){
      this.$router.goBack();
    },
  }
}
</script>

<style lang="scss" scoped>
	.setUp-wrap{
	}
    .content-wrap{
    	padding: 0 31px ;
    	margin-top: 90px;
    	.mint-cell{
    		background: #1a3132;
    		border-bottom: 2px solid #021b1c;
    		height: 92px;top:28px;
    		color:#fff;
    		& /deep/ .mint-cell-title{
    			line-height: 80px;
    		 
    		}
    		& /deep/ .mint-cell-wrapper{
    			padding-left:21px;
    			
    		}
    		& /deep/ .mint-cell-text{
    			font-size: 30px;
    			
    		}
    		& /deep/ .mint-cell-allow-right::after{
    			width: 19px;
    			height: 19px;
    			right: 25px;
    		}
    	}
    	.last{
    		border:none
    	}
    }
</style>



