<template>
  <div class="footerTab bd-top-1">
    <div class="tab-item">
      <router-link to='/home'>
        <i class="iconfont icon-home"></i>
        <span>首页</span>
      </router-link>
    </div>
    <div class="tab-item">
      <router-link to='/expert'>
        <i class="iconfont icon-xiaoxi"></i>
        <span>专家咨询</span>
      </router-link>
    </div>
    <div class="tab-item">
      <router-link to='/mine'>
        <i class="iconfont icon-user"></i>
        <span>我的</span>
      </router-link>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'footertab'
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/_mixin.scss";
  .footerTab {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100px;
    box-sizing: border-box;
    overflow: hidden;
    line-height: 100px;
    z-index: 888;
    border-top: 1px #808d8d solid; /*px*/
    background-color: #1c2525;
    .tab-item {
      width: 33.333333%;
      height: 100%;
      float: left;
      text-align: center;
      position: relative;
      a {
        color: #cbcbcb;
      }
      i {
        display: block;
        font-size: 50px;
        line-height: normal;
        margin: 7px auto 0 auto;
      }
      span {
        @include font-dpr(22px);
        /*font-size: 22px;*/
        line-height: 1.5em;
        display: block;
        /*height: 22px;*/
        /*line-height: 22px;*/
      }
    }
  }

  .footerTab .tab-item a.router-link-active {
    color: #34e0db;
  }

</style>



