<template>
    <div class="mine-wrap">
       <mt-header title="我的">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="mine-top">
    	<ul class="mine-topL">
    		<li class="title-wrap">鹰潭农业和粮食局</li>
    		<li><i class="iconfont icon-phone"></i>070-6212278</li>
    		<li><i class="iconfont icon-message"></i>ytnyj@yingtan.gov.cn</li>
    		<li><i class="iconfont icon-map"></i>鹰潭市信江新区经济大厦</li>
    	</ul>
    	<div class="mine-topR">
    		<p><img src="../../assets/imgs/mine_logo.png"/></p>
    	</div>
    </div>
    <div class="mine-content">
    	<div class=""  @click="change" >
    	  <mt-cell class="cell-wrap" title="我的收藏"  is-link><i class="iconfont icon-heart"></i></mt-cell>
    	</div>
    	<mt-cell class="cell-wrap" title="推荐给朋友" :to="{path:'/mine/recommend'}" is-link><i class="iconfont icon-good"></i></mt-cell>
    	<mt-cell class="cell-wrap" title="设置" :to="{path:'/mine/setUp'}" is-link><i class="iconfont icon-set"></i></mt-cell>
    	
    </div>
    <div class="mine-content">
    	<mt-cell class="cell-wrap" title="关于我们"  is-link><i class="iconfont icon-personnel"></i></mt-cell>
    	<mt-cell class="cell-wrap" title="版本更新"  is-link><i class="iconfont icon-time"></i></mt-cell>
    	<mt-cell class="cell-wrap last" title="用户帮助"  is-link><i class="iconfont icon-Help"></i></mt-cell>
    	
    </div>
    </div>
</template>

<script>
export default {
	name: 'mineInfo',
    data() {
      return {
      	to:null,
      }
    },
    created() {
    },
    mounted(){
    	
    },
    methods: {
      change(){
      	this.$router.push({
      		path:'/mine/collect',
      		query:{id:1}
      	})
      }
    },
 
}
</script>

<style lang="scss" scoped>
  .mine-wrap{
  	color:#fff;
    .mine-top{
    	height: 235px;
    	margin:20px 30px;
    	margin-top: 110px;
    	background: #1A3132;
    	display: flex;
    	justify-content: space-between;
    	box-sizing: border-box;
    	.mine-topL{
    		padding-left: 31px;
    		.title-wrap{
    			padding-top: 10px;
    			font-size: 31px;
    			font-weight: bold;
    			line-height: 80px;
    		}
    		li{
    			line-height: 42px;
    			font-size: 24px;
    			.iconfont{
    				font-size: 24px;
    				color:#597273;
    				margin-right: 12px;
    			}
    		}
    	}
    	.mine-topR{
    		p{
    			padding-top: 37px;
    			padding-right: 21px;
    			img{
    				width: 110px;
    			}
    		}
    	}
    }
    .mine-content{
    	padding: 0 30px;
    	margin-bottom: 20px;
    	
    	.mint-cell{
    		i{
    			font-size: 34px;
    			position: absolute;
    			left: 21px;top:28px;
    			color:#35E9E3;
    		}
    		background: #1a3132;
    		border-bottom: 2px solid #021b1c;
    		height: 92px;
    		& /deep/ .mint-cell-title{
    			line-height: 80px;
    		
    		}
    		& /deep/ .mint-cell-wrapper{
    			padding-left:75px;
    			
    		}
    		& /deep/ .mint-cell-text{
    			font-size: 30px;
    		}
    		& /deep/ .mint-cell-allow-right::after{
    			width: 19px;
    			height: 19px;
    			right: 25px;
    		}
    	}
    	.last{
    		border:none
    	}
    }
  }
</style>



