<template>
  <div class="weather-wapper">
    <mt-header title="墒情预报">
      <router-link to="/home/weather" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="echart-container" id="echarts">
      温度
    </div>
  </div>
</template>

<script>
  export default {
    name: "weather",
    data() {
      return {}
    },
    methods: {
      initPage() {
        let myChart = this.$echarts.init(document.getElementById('echarts'));
        var datax = ['8日', '9日', '10日', '11日', '12日', '13日', '14日'];
        var dataser1 = [90, 90, 90, 90, 90, 90, 90];
        var dataser2 = [61.2, 67.2, 62.2, 63.2, 61.2, 63.2, 64.2, 61.6, 61.2, 61.8, 61.3, 62.2]
        var dataser3 = [71.2, 72.2, 72.2, 71.2, 71.2, 73.2, 71.2, 71.6, 71.2, 71.8, 71.3, 72.2]
        var dataser4 = [76.2, 78.2, 76.2, 75.2, 76.2, 77.2, 78.2, 77.6, 78.2, 76.8, 76.3, 78.2]
        var dataser5 = [81.2, 87.2, 82.2, 83.2, 81.2, 83.2, 84.2, 81.6, 81.2, 81.8, 81.3, 82.2]
        var option = {
          title: {
            text: '各深度土壤相对湿度（%）',
            textStyle: {
              color: '#ffffff',
              lineHeight: 56,
              fontSize: '14',
              fontWeight: '400',
            },
            left: 'center',
            top: '18'
          },
          legend: {
            right: "10%",
            top: "8%",
            icon: 'cicle',
            iconSize: 5,
            itemWidth: 8,
            textStyle: {
              fontSize: 12,
              color: "#fff",
              fontFamily: "微软雅黑"
            },
            data: [
              {
                name: "20CM",
                icon: 'circle'
              },
              {
                name: "46CM",
                icon: 'circle'
              },
              {
                name: "60CM",
                icon: 'circle'
              },
              {
                name: "80CM",
                icon: 'circle'
              }
            ],
            itemGap: 5,
            textStyle: {
              color: "#bffffb",
              fontSize: 12
            },
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              lineStyle: {
                color: '#ccc'
              }
            }
          },
          grid: {
            left: '3%',
            right: '3%',
            bottom: '4%',
            top: '80',
            containLabel: true
          },
          xAxis: {
            type: 'category',
            axisPointer: {
              show: false
            },
            show: true,
            lineStyle: {
              color: '#192d35'
            },
            axisTick: {
              show: false,
              inside: true
            },
            axisLabel: {
              color: "#bffffb"
            },
            data: datax
          },
          yAxis: [
            {
              name: '土壤深度',
              nameTextStyle: {
                color: '#bffffb'
              },
              type: 'value',
              axisLine: {
                show: true,
                lineStyle: {
                  color: '#192d35'
                }
              },
              axisTick: {
                show: false,
                inside: true
              },
              axisLabel: {
                color: "#bffffb"
              },
              splitLine: {
                show: false
              }
            }
          ],
          series: [
            {
              name: '数量',
              type: 'bar',
              itemStyle: {
                normal: {
                  color: '#092223',
                }
              },
              barWidth: '50%',
              label: {
                normal: {
                  show: false,
                  color: ['#ff8400', '#4b79ab'],
                  fontSize: 12,
                  position: 'top',
                }
              },
              data: dataser1
            },
            {
              name: "80CM",
              type: 'line',
              itemStyle: {
                normal: {
                  color: '#dfcb1e',
                }
              },
              label: {
                normal: {
                  show: true,
                  fontSize: 12,
                  position: 'top',
                }
              },
              symbolSize: 10,
              data: dataser2
            },
            {
              name: "60CM",
              type: 'line',
              itemStyle: {
                normal: {
                  color: '#1cdab5',
                }
              },
              label: {
                normal: {
                  show: true,
                  fontSize: 12,
                  position: 'top',
                }
              },
              symbolSize: 10,
              data: dataser3
            },
            {
              name: "46CM",
              type: 'line',
              itemStyle: {
                normal: {
                  color: '#f79523',
                }
              },
              label: {
                normal: {
                  show: true,
                  fontSize: 12,
                  position: 'top',
                }
              },
              symbolSize: 10,
              data: dataser4
            },
            {
              name: "20CM",
              type: 'line',
              itemStyle: {
                normal: {
                  color: '#32a1ec',
                }
              },
              label: {
                normal: {
                  show: true,
                  fontSize: 12,
                  position: 'top',
                }
              },
              symbolSize: 10,
              data: dataser5
            }
          ]
        }
        myChart.setOption(option);
      }
    },
    components: {},
    mounted() {
      this.initPage()
    }
  }
</script>

<style lang="scss" scoped>
  .weather-wapper {
    /*margin-top:1.2rem;*/
    padding-top: 1.2rem;
    color: #fff;
  }

  .echart-container {
    height: calc(100vh - 130px);
  }

</style>


