<template>
    <div class="anotherPage">
       <mt-header title="专家咨询">
          <mt-button icon="back" slot="left"  @click="back"></mt-button>
       </mt-header>
       <div class="content">
          <div class="message" :style="borderStyle">
              <textarea placeholder="请输入您的留言信息"></textarea>
          </div>
           <div class="information" :style="borderStyle">
               <input type="text" placeholder="请输入您的联系方式（选填）"/>
           </div>
           <div class="btn">
               <button :style="btnBorderStyle">提交</button>
               <button :style="btnBorderStyle">取消</button>
           </div>
           <div class="MyMessage">
               <p>我的留言</p>
               <ul>
                   <li v-for="list in messageData" @click="change">
                       <p><span class="test">{{list.xx}}</span>
                           <span v-if="list.replyIf=='已回复'" class="replyIf">{{list.replyIf}}</span>
                           <span v-if="list.replyIf=='未回复'" class="replyIf2">{{list.replyIf}}</span>
                       </p>
                       <p><span class="mar20">咨询时间: {{list.consultationDate}}</span><span>回复时间：{{list.reply}}</span></p>
                   </li>
               </ul>
           </div>
       </div>
    </div>
</template>
<script>
export default {
  name: "mineInfo",
  data() {
    return {
      borderStyle:'border: 1px solid #3b898c;',
      btnBorderStyle:'border: 1px solid #17adbf !important;',
      messageData: [
        {
          xx: "请问如何了解当地相关农业政策信息？",
          replyIf: "已回复",
          consultationDate: "2017-11-20",
          reply: "2017-11-20"
        },
        {
          xx: "请问如何了解当地相关农业政策信息？",
          replyIf: "未回复",
          consultationDate: "2017-11-20",
          reply: "暂未回复"
        },
        {
          xx: "请问如何了解当地相关农业政策信息？",
          replyIf: "已回复",
          consultationDate: "2017-11-20",
          reply: "2017-11-20"
        },
        {
          xx: "请问如何了解当地相关农业政策信息？",
          replyIf: "已回复",
          consultationDate: "2017-11-20",
          reply: "2017-11-20"
        }
      ]
    };
  },
  created() {},
  methods: {
    back() {
      this.$router.goBack();
    },
    change() {
      this.$router.push({
        path: "/expert/myMessage"
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.anotherPage {
  /*margin-top: 1.2rem;*/
  width: 100%;
  /*height: 1400px;
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;*/
  color: #fff;
  background-color: #021b1c;
  .content {
    height: 100%;
    margin: 90px 0 100px;
    overflow: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }
}
.message {
  width: 100%;
  height: 220px;
  background-color: #0e2627;
  padding: 25px 20px 20px 20px;
  box-sizing: border-box;
}
.message textarea {
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: none;
  color: #7bb8b6;
  font-size: 30px;
}
.information {
  width: 100%;
  height: 88px;
  background-color: #0e2627;
  padding: 0 20px;
  box-sizing: border-box;
  margin-top: 20px;
}
.information input {
  width: 100%;
  height: 100%;
  background: none;
  border: none;
  font-size: 26px;
  color: #7bb8b6;
}
.btn {
  width: 100%;
  text-align: center;
  margin-top: 50px;
}
.btn > button {
  width: 40%;
  height: 70px;
  line-height: 50px;
  text-align: center;
  color: white;
  font-size: 30px;
  border-radius: 130px;
  background: none;
}
.btn > button:first-child {
  background: linear-gradient(to right, #2fcec9, #16aabf);
}
.btn > button:last-child {
  background: linear-gradient(to right, #0b3e3e, #06383d);
  color: #17adbf;
}
.MyMessage {
  width: 100%;
}
.MyMessage > p {
  width: 100%;
  height: 30px;
  border-left: 5px solid #e9cd10;
  padding-left: 20px;
  line-height: 30px;
  font-size: 32px;
  margin: 70px 0 40px 0;
}
.MyMessage > ul {
  width: 100%;
}
.MyMessage > ul > li {
  width: 100%;
  background-color: #1a3132;
  padding: 40px 20px;
  margin-bottom: 20px;
  box-sizing: border-box;
}
.MyMessage > ul > li > p:first-child {
  width: 100%;
  font-size: 29px;
  position: relative;
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #7bb8b6;
}
.replyIf {
  position: absolute;
  right: 30px;
  top: 2px;
  font-size: 24px;
  color: #35ece6;
}
.replyIf2 {
  position: absolute;
  right: 30px;
  top: 2px;
  font-size: 24px;
  color: #e9cd10;
}
.MyMessage > ul > li > p:last-child {
  font-size: 24px;
  color: #cccccc;
  margin-top: 30px;
  width: 90%;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.test {
  width: 80%;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.mar20 {
  margin-right: 20px;
}
.content {
  width: 100%;
  box-sizing: border-box;
  height: calc(100vh - 190px);
  overflow: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
</style>



