<template lang="html">
  <div class="anotherPage">
    <mt-header title="咨询信息">
      <mt-button icon="back" slot="left" @click="back"></mt-button>
    </mt-header>
    <div class="scroll-box">
      <div class="content">
        &nbsp;&nbsp;我的留言
      </div>

      <div class="message" :style="borderStyle">
        <div class="arrow">
          <em>◆</em><span>◆</span>
        </div>
        <textarea placeholder="请问如何了解当地相关农业政策信息？"></textarea>
        <div>日期：2017-11-20</div>
      </div>
      <div class="content">
        &nbsp;&nbsp;留言回复
      </div>
      <div class="message" :style="borderStyle">
        <div class="arrow">
          <em>◆</em><span>◆</span>
        </div>
        <textarea placeholder="可以向当地相关主管部门科室进行资料索取。"></textarea>
        <div>日期：2017-11-20</div>
      </div>
    </div>

  </div>
</template>
<script>
  export default {
    data() {
      return {
        borderStyle: 'border: 1px solid #3b898c;'
      }
    },
    methods: {
      back() {
        this.$router.goBack();
      }
    }
  };
</script>

<style lang="scss" scoped>
  .anotherPage {
    /*margin-top: 1.2rem;*/
    /*position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;*/
    color: #666;
    .scroll-box {
      height: 100%;
      margin: 90px 0 100px;
      overflow: hidden;
      overflow-y: auto;
      -webkit-overflow-scrolling: touch;
    }
    .message {
      width: 92%;
      height: 200px;
      margin: 49px 4%;
      background: #021b1c;
      color: #fff;
      font-size: 26px;
      padding: 27px 0 20px 30px;
      box-sizing: border-box;
      position: relative;

      .arrow {
        position: absolute;
        width: 40px;
        height: 40px;
        left: 10%;
        top: -40px;
        overflow: hidden;
      }

      .arrow * {
        position: absolute;
        /*font-size: 40px;在华为手机上会导致箭头显示有问题,偏小*/
        /*line-height: 40px;在华为手机上会导致箭头显示有问题,偏小*/
        /*width: 40px;在华为手机上会导致箭头显示有问题,偏小*/
      }

      .arrow em {
        color: #3b898c;
        top: 28px;
      }

      .arrow span {
        color: #021b1c;
        top: 30px;
      }

      div {
        position: absolute;
        right: 30px;
        bottom: 20px;
        color: #ccc;
      }

    }
    .message textarea {
      width: 100%;
      height: 100%;
      background-color: transparent;
      border: none;
      color: #fff;
      font-size: 26px;
    }

    .content {
      height: 33px;
      border-left: 6px solid #e9cd10;
      color: #fff;
      padding: 0px;
      margin: 49px 30px 21px;
      font-size: 32px;
    }

    .message textarea::-webkit-input-placeholder {
      color: #fff;
    }

    .message textarea:-moz-placeholder {
      color: #fff;
    }

    .message textarea::-moz-placeholder {
      color: #fff;
    }

    .message textarea:-ms-input-placeholder {
      color: #fff;
    }

  }
</style>



