<template lang="html">
    <div class="recommend-wrap">
       <mt-header title="推荐给朋友">
          <mt-button icon="back" slot="left" @click="back"></mt-button>
       </mt-header>
       <div class="content-wrap">
	    	<mt-cell class="cell-wrap" title="微博" to="http://service.weibo.com/share/share.php?title=http://192.168.1.100:9990/#/mine" is-link>
	    		<i class="iconfont icon-weibo"></i>
	    	</mt-cell>
	    	<mt-cell class="cell-wrap" title="微信"  is-link><i class="iconfont icon-wechat"></i></mt-cell>
	    	<mt-cell class="cell-wrap" title="朋友圈"  is-link><i class="iconfont icon-pengyouquan0"></i></mt-cell>
	    	<mt-cell class="cell-wrap last" title="QQ" to="http://connect.qq.com/widget/shareqq/index.html?url=http://192.168.1.100:9990/#/mine&sharesource=qzone&title=分享测试" is-link><i class="iconfont icon-qq"></i></mt-cell>
       </div>
    </div>
</template>
<script>
export default {
	 mounted () {
    },
    methods: {
    	back(){
    	  this.$router.goBack();
    	},
    }
}
</script>

<style lang="scss" scoped>
	.recommend-wrap{
	}
    .content-wrap{
    	margin-top: 90px;
    	padding: 20px 31px 0;
    	.mint-cell{
    		background: #1a3132;
    		border-bottom: 2px solid #021b1c;
    		height: 92px;
    		color:#fff;
    		i{
    			font-size: 36px;
    			position: absolute;
    			left: 21px;top:28px;
    			color:#35E9E3;
    		}
    		& /deep/ .mint-cell-title{
    			line-height: 80px;
    		 
    		}
    		& /deep/ .mint-cell-wrapper{
    			padding-left:75px;
    			
    		}
    		& /deep/ .mint-cell-text{
    			font-size: 30px;
    			
    		}
    		& /deep/ .mint-cell-allow-right::after{
    			width: 19px;
    			height: 19px;
    			right: 25px;
    		}
    	}
    	.last{
    		border:none
    	}
    }
</style>



