<template>
    <div class="specialist">
        <mt-header title="专家列表">
        	<mt-button icon="back" slot="left" @click="back"></mt-button>
        </mt-header>
        <div class="scroll-box">
           <ul>
             <li @click="changeTwo" v-for="list in lists">
               <div class="pic">
                 <img :src='list.pic'/>
               </div>
               <div class="jiaoshou">
                 <div class="news">
                  <span>{{list.name}}</span>
                  <span>{{list.specialist}}</span>
                 </div>
                 <div class="guard">{{list.guard}}</div>
                 <div class="questions">
                  <span>提问量：<span class="answer">{{list.questions}}</span></span>
                  <span>回复量：<span class="answer">{{list.reply}}</span></span>
                 </div>
               </div>
               <div class="right-arrow">
                 <i class="iconfont icon-weixin"></i>
               </div>
             </li>
           </ul>
        </div>
    </div>
</template>

<script>
export default {
  name: "expert",
  data() {
    return {
      msg: "",
      lists: [
        {
          pic: require("../../assets/imgs/professor.png"),
          name: "郭昕怡",
          specialist: "副教授",
          guard: "养鸡及疾病预防",
          questions: "5",
          reply: "2"
        },
        {
          pic: require("../../assets/imgs/professor1.png"),
          name: "李云弗",
