          name: "逯明辉",
          specialist: "教授",
          guard: "种植番茄",
          questions: "5",
          reply: "2"
        },
        {
          pic: require("../../assets/imgs/professor3.png"),
          name: "周庆安",
          specialist: "教授",
          guard: "养殖鸡、猪",
          questions: "5",
          reply: "2"
        },
        {
          pic: require("../../assets/imgs/professor.png"),
          name: "牛华锋",
          specialist: "副教授",
          guard: "养鸡及疾病预防",
          questions: "5",
          reply: "2"
        },
        {
          pic: require("../../assets/imgs/professor1.png"),
          name: "蔡震云",
          specialist: "教授",
          guard: "养鸡及疾病预防",
          questions: "5",
          reply: "2"
        },
        {
          pic: require("../../assets/imgs/professor2.png"),
          name: "曾小玲",
          specialist: "教授",
          guard: "稻田的病虫害防治",
          questions: "5",
          reply: "2"
        },
        {
          pic: require("../../assets/imgs/professor3.png"),
          name: "张立伟",
          specialist: "副教授",
          guard: "养鸡及疾病预防",
          questions: "6",
          reply: "4"
        },
        {
          pic: require("../../assets/imgs/professor.png"),
          name: "李云弗",
          specialist: "副教授",
          guard: "养鸡及疾病预防",
          questions: "5",
          reply: "2"
        }
      ]
    };
  },
  // created() {
  //   this.msg = hello("欢迎来到");
  // },
  mountd() {

  },
  methods: {
    back() {
      this.$router.goBack();
    },

    changeTwo() {
      this.$router.push({
        path: "/expert/expertDetails"
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.specialist {
  /*margin-top: 1.2rem;*/
  width: 100%;
  /*height: 1400px;
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;*/
  color: #fff;
  background-color: #021b1c;
  .scroll-box {
    height: 100%;
    margin: 90px 0 100px;
    overflow: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }
  ul {
    padding: 20px 30px;
    width: 100%;
    box-sizing: border-box;
    /*height: calc(100vh - 190px);*/
    overflow: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }
  ul li {
    width: 100%;
    height: 180px;
    background: #1a3132;
    margin-top: 20px;
    color: #fff;
    display: flex;
    .pic {
      width: 170px;
      margin: 20px 30px 50px;
      object-fit: cover;
      object-position: 100% 0;
      img {
        width: 80%;
        // height: 100%;
        object-fit: cover;
        object-position: 50% 0;
      }
    }
    .jiaoshou {
      margin: 21px 50px 35px 0;
      font-size: 20px;
      div {
        line-height: 45px;
      }
      .news {
        width: 225px;
        display: block;
        span:nth-child(1) {
          font-size: 32px;
        }
        span:nth-child(2) {
          color: #35ece6;
          margin-left: 25px;
        }
      }
    }
    .questions {
      color: #d5faf9;
      .answer {
        color: #fff;
        margin-left: 17px;
      }
      span:nth-child(1) {
        margin-right: 28px;
      }
    }
    .right-arrow i {
      font-size: 50px;
      line-height: 180px;
      cursor: pointer;
      color: #fff;
    }
  }
  li:nth-child(1) {
    margin-top: 0px;
  }
}
</style>



