<template>
  <div class="news-wrap">
    <mt-header title="农业资讯">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="scroll-box">
      <newsList :pageUrl="url"></newsList>
    </div>
  </div>
</template>

<script>
  import newsList from 'components/newsList/newsList.vue';
  export default {
    name: "news",
    data() {
      return {
        url: 'getPolicyList'
      }
    },
    components: {
      newsList
    }
  }
</script>

<style lang="scss" scoped>
  /*@import "../../assets/css/_mixin.scss";*/
  .news-wrap {
    background-color: #021b1c;
    .scroll-box {
      /*height: calc(100% - 190px);*/
      /*height: calc(100vh - 190px);*/
      height: 100%;
      margin: 90px 0 100px;
      overflow: hidden;
      overflow-y: auto;
      -webkit-overflow-scrolling: touch;
    }
  }
</style>


