<template>
    <div class="border-title-box">
        <span class="border"></span>
        <span class="title">{{title}}</span>
        <slot></slot>
    </div>
</template>
<script>
    export default{
        name: 'borderTitle',
        props: {
            title: {
                type: String,
                default: ''
            }
        },
        data(){
            return {}
        }
    }
</script>
<style lang="scss" scoped>
    .border-title-box {
        box-sizing: border-box;

    .border {
        vertical-align: bottom;
        display: inline-block;
        width: 6px;
        height: 30px;
        background-color: #e9cd10;
    }

    .title {
        font-size: 32px;
        line-height: 20px;
        letter-spacing: 0;
        color: #ffffff;
        margin-left: 15px;
        margin-right: 10px;
    }

    }

</style>


