<template lang="html">
    <div class="collect-wrap">
       <mt-header title="我的收藏">
          <mt-button icon="back" slot="left" @click="back"></mt-button>
       </mt-header>
       <!--<div class="content-wrap">
           <ul class="list-wrap">
           	<li v-for="(val,index) in listData">
           		<div class="list-left">
           			<img :src="val.img"/>
           		</div>
           		<div class="list-right">
           			<p class="list-title">{{val.title}}</p>
           			<p class="list-num"><i class="iconfont icon-eye"></i>{{val.num}}</p>
           		</div>
           	</li>
           </ul>
       </div>-->
       
       <div class="scroll-box">
        <newsList :pageUrl="url"></newsList>
    </div>
    </div>
</template>
<script>
	import newsList from 'components/newsList/newsList.vue';
    export default {
	  name: 'collect',
      data() {
         return { 
      	    listData:[
      	       {img:require('../../assets/imgs/mine_list_01.png'),title:'11月暗藏波涛的板栗市场！板栗还涨价吗？三个原因告诉你真相！',num:'2198'},
      	       {img:require('../../assets/imgs/mine_list_02.png'),title:'听说新玉米涨到60元/吨？还涨吗？养殖户啥时囤货合适?',num:'1218'},
      	       {img:require('../../assets/imgs/mine_list_03.png'),title:'上海稻田产业贸易有限公司位于上海市层西南部位。',num:'3218'},
      	       {img:require('../../assets/imgs/mine_list_04.png'),title:'东宁市在对俄经贸合作、特色农业升级等方面获得重大突破。',num:'2021'},
      	       {img:require('../../assets/imgs/mine_list_05.png'),title:'听说新玉米涨到60元/吨？还涨吗？养殖户啥时囤货合适？',num:'1210'}
      	    ],
      	    url:'collectList'
         }
      },
	  methods:{
	    back(){
	      this.$router.goBack();
	    },
	  },
	  components: {
        newsList
      }
    }
</script>

<style lang="scss" scoped>
    .collect-wrap{
    	.scroll-box {
	      /*height: calc(100vh - 190px);*/
	      height: 100%;
	      margin: 90px 0 100px;
	      overflow: hidden;
	      overflow-y: auto;
	      -webkit-overflow-scrolling: touch;
	    }
    	.content-wrap{
    		margin-top: 90px;
    		padding: 0 31px;
    		padding-top:20px;
    		li{
    			height: 160px;
    			background: #1a3132;
    			margin-bottom: 20px;
    			display: flex;
    			justify-content: space-between;
    			.list-left{
    				width: 230px;
    				img{
    					width: 230px;
    					height: 160px;
    				}
    			}
    			.list-right{
    				height: 160px;
    				box-sizing: border-box;
    				color: #fff;
    				padding:20px 0;
    				padding-right: 34px;
    				padding-left: 32px;
    				font-size: 28px;
    				display: flex;
    				flex-wrap: wrap;
    				align-content: space-between ;
    				p{
    					width: 100%;;
    				}
    				.list-title{
    					height: calc(100% - 24px);
    					line-height: 34px;
    					padding-top: 6px;
    					display: -webkit-box;
						-webkit-box-orient: vertical;
						-webkit-line-clamp: 3;
						overflow: hidden;
    				}
    				.list-num{
    					font-size: 20px;
    					text-align: right;
    					height: 24px;
    					.iconfont{
    						font-size: 16px;
    						color:#34E5E0;
    						margin-right: 10px;
    					}
    				}
    			}
    		}
    	}
    }
</style>



