<template lang="html">
    <div class="setUp-wrap">
       <mt-header title="字号">
          <mt-button icon="back" slot="left" @click="back"></mt-button>
       </mt-header>
       <div class="content-wrap">
       	<div>
       		<div class="title-wrap">正文字号</div>
       		<p class="message-wrap">拖动下面的滑块，可设置字体大小</p>
       		<div class="range-box">
       			<div class="range-left">{{size[rangeValue - 16]}}</div>
		       	  <mt-range
		       	  	class="range-right"
					  v-model="rangeValue"
					  :value='18'
					  :min="16"
					  :max="22"
					  :step="1"
					  :bar-height="5">
					  <!--<div slot="start">16</div>
		              <div slot="end">22</div>-->
				  </mt-range>
       			
       		</div>
       	</div>
       </div>
    </div>
</template>
<script>
export default {
	data(){
		return{
			rangeValue:18,
			size:['最小','小号',"偏小","标准","偏大","大号","最大"]
		}
	},
  methods:{
    back(){
      this.$router.goBack();
    },
  }
}
</script>

<style lang="scss" scoped>
    .content-wrap{
    	padding: 0 31px ;
    	margin-top: 110px;
    	&>div{
    		padding:30px 20px;
    		background: #1A3132;
    		color:#fff;
    		.title-wrap{
    			font-size: 30px;
    		}
    		.message-wrap{
    			margin-top: 20px;
    			font-size: 24px;
    			text-align: center;
    		}
    		.range-box{
    			margin-top: 20px;
    			display: flex;
    			justify-content: space-between;
    			.range-left{
    				font-size: 24px;
    				padding-top: 32px;
    				width: 80px;
    				text-align: center;
    			}
    			.range-right{
    				font-size: 24px;
    				width: calc(100% - 90px);
    				color:#fff;
    				margin:30px 0 10px;
    			}
    			
    		}
    	}
    }
</style>



