<template>
  <div class="weather-wapper">
    <mt-header title="农事预报">
      <router-link to="/home/weather" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="forecast-info">
      <table>
        <thead>
        <tr>
          <th v-for="item in titleList">{{item}}</th>
          <th>
            <div class="last-th">虫害预防</div>
          </th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in list">
          <td>{{item.col1}}</td>
          <td :class="[item.col2=='不宜'?'yellow':'blue']">{{item.col2}}</td>
          <td :class="[item.col3=='不宜'?'yellow':'blue']">{{item.col3}}</td>
          <td :class="[item.col4=='不宜'?'yellow':'blue']">{{item.col4}}</td>
          <td :class="[item.col5=='不宜'?'yellow':'blue']">{{item.col5}}</td>
          <td :class="[item.col6=='不宜'?'yellow':'blue']">{{item.col6}}</td>
          <td :class="[item.col7=='不宜'?'yellow':'blue']">{{item.col7}}</td>
        </tr>
        </tbody>
      </table>
    </div>

  </div>
</template>

<script>
  export default {
    name: "weather",
    data() {
      return {
        titleList: ['分类', '耕地', '播种', '定植', '施肥', '灌溉'],
        list: [
          {col1: '8日', col2: '宜', col3: '-', col4: '不宜', col5: '不宜', col6: '宜', col7: '不宜'},
          {col1: '9日', col2: '不宜', col3: '宜', col4: '-', col5: '宜', col6: '不宜', col7: '不宜'},
          {col1: '10日', col2: '宜', col3: '宜', col4: '不宜', col5: '宜', col6: '不宜', col7: '-'},
          {col1: '11日', col2: '不宜', col3: '宜', col4: '宜', col5: '-', col6: '不宜', col7: '不宜'},
          {col1: '12日', col2: '宜', col3: '宜', col4: '-', col5: '宜', col6: '-', col7: '不宜'},
          {col1: '13日', col2: '-', col3: '宜', col4: '不宜', col5: '-', col6: '宜', col7: '不宜'},
          {col1: '14日', col2: '-', col3: '宜', col4: '不宜', col5: '宜', col6: '-', col7: '不宜'}
        ]
      }
    },
    components: {}
  }
</script>

<style lang="scss" scoped>
  .weather-wapper {
    /*margin-top:1.2rem;*/
    padding-top: 1.2rem;
    color: #fff;
    .forecast-info {
      padding: 30px;
      width: 690px;
      table {
        width: 100%;
        margin-top: 36px;
        width: 100%;
      }
      table tr {
        border-bottom: solid 3px #29374a;
      }
      thead {
        background: #005853;
      }
      tbody tr {
        height: 98px;
        line-height: 98px;
        text-align: center;
      }
      tr th {
        height: 80px;
        line-height: 90px;
        font-size: 26px;
      }
      .last-th {
        width: 60px;
        line-height: 30px;
        display: inline-block;
        box-sizing: border-box;
        transform: translateY(16px);
      }
      table tbody tr td:nth-child(1) {
        color: #cfe2ff;
      }
      table tbody tr td:nth-child(odd) {
        background: #092223;
      }
      .blue {
        color: #38f2ec;
      }
      .yellow {
        color: #ffe328;
      }
    }
  }

</style>


