import Vue from "vue";
import Router from "vue-router";
import MainComps from "components/main/mainComps";
import FooterTab from "components/tab/footerTab";
import Login from "pages/login/login";
import Home from "pages/home/home";
import Expert from "pages/expert/expert";
import Mine from "pages/mine/mine";
import expertDetails from "components/expertDetails/expertDetails";
import News from "pages/news/news";
import Policy from "pages/policy/policy";
import Article from "components/article";
import Weather from "pages/weather/weather";
import Collect from "components/mineDetails/collect";
import Recommend from "components/mineDetails/recommend";
import SetUp from "components/mineDetails/setUp";
import FontSize from "components/mineDetails/fontSize";
import MyMessage from "components/expertDetails/myMessage";
import ColdWaveWarning from "components/weatherDetails/coldWaveWarning";
import CropForecast from "components/weatherDetails/cropForecast";
import WeatherForecast from "components/weatherDetails/weatherForecast";
import Humidity from "components/weatherDetails/weatherForecastDetails/humidity";
import Precipitation from "components/weatherDetails/weatherForecastDetails/precipitation";
import Temperature from "components/weatherDetails/weatherForecastDetails/temperature";
import Wind from "components/weatherDetails/weatherForecastDetails/wind";
import MoistureForecast from "components/weatherDetails/moistureForecast";

Vue.use(Router);

Router.prototype.goBack = function () {
  this.isBack = true;
  window.history.go(-1);
};
export default new Router({
  routes: [
    {

      path: '/',
      redirect: '/home'
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '/home',
      // name: 'home',
      component: MainComps,
      children: [
        {
          path: '',
          components: {
            body: Home,
            footer: FooterTab
          }
        },
        {
          path: 'news',
          components: {
            body: News,
            footer: FooterTab
          }
        },
        {
          path: 'policy',
          components: {
            body: Policy,
            footer: FooterTab
          },
        },
        {
          path: 'articles',
          components: {
            body: Article,
            footer: FooterTab
          }
        },
        {
          path: 'weather',
          components: {
            body: Weather,
            footer: FooterTab
          }
        },
        {
          path: 'coldWaveWarning',
          components: {
            body: ColdWaveWarning
          }
        },
        {
          path: 'weatherForecast',
          components: {
            body: WeatherForecast
          },
          redirect: '/home/weatherForecast/temperature',
          children: [
            /*{
              path: '',
              components: {
                weatherContent: Temperature
              }
            },*/
            {
              path: 'temperature',
              components: {
                weatherContent: Temperature
              }
            },
            {
              path: 'humidity',
              components: {
                weatherContent: Humidity
              }
            },
            {
              path: 'precipitation',
              components: {
                weatherContent: Precipitation
              }
            },
            {
              path: 'wind',
              components: {
                weatherContent: Wind
              }
            }
          ]
        },
        {
          path: 'moistureForecast',
          components: {
            body: MoistureForecast
          }
        },
        {
          path: 'cropForecast',
          components: {
            body: CropForecast
          }
        },
      ]
    },
    {
      path: '/expert',
      // name: 'expert',
      component: MainComps,
      children: [
        {
          path: '',
          components: {
            body: Expert,
            footer: FooterTab
          }
        },
        {
          path: 'expertDetails',
          components: {
            body: expertDetails,
            footer: FooterTab
          }
        },
        {
          path: 'myMessage',
          components: {
            body: MyMessage,
            footer: FooterTab
          }
        }
      ]
    },
    {
      path: '/mine',
      // name: 'mine',
      component: MainComps,
      children: [
        {
          path: '',
          components: {
            body: Mine,
            footer: FooterTab
          }
        },
        {
          path: 'collect',
          components: {
            body: Collect,
          }
        },
        {
          path: 'recommend',
          components: {
            body: Recommend,
          }
        },
        {
          path: 'setUp',
          components: {
            body: SetUp,
          }
        },
        {
          path: 'fontSize',
          components: {
            body: FontSize,
          }
        },

      ]
    },
  ]
})



