<template>
    <div class="weather-main">
        <mt-header title="农业气象">
            <router-link to="/" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>

        <div class="weather-body">
            <div class="top-content">
                <div class="flex-item">
                    <div class="date"><p><span>{{date}}</span><span class="mgl-16">{{week}}</span></p>
                        <p>农历{{cnDate}}</p></div>
                </div>
                <div class="flex-item">
                    <img src="../../assets/imgs/blue_warning.png" alt="" class="warning-img">
                    <router-link tag="div" to="coldWaveWarning" class="warning-txt"><p>寒潮预警</p>
                        <p>{{warningLevel}}</p></router-link>
                </div>
                <div class="flex-item">
                    <div class="hint">
                        <p>{{solarTerm}}</p>
                        <p>{{hint}}</p>
                    </div>
                </div>
            </div>
            <div class="weather-indicator">
                <div class="indicator-item forecast"><span>{{weatherName}}</span> <i class="iconfont icon-qing"></i>
                </div>
                <div class="indicator-item forecast"><p>{{forecast1}}</p>
                    <p class="white-txt">{{forecast2}}</p><i class="iconfont icon-tiwen"></i></div>
                <div class="indicator-item forecast"><p>{{direction1}}</p>
                    <p class="white-txt">{{direction2}}</p><i class="iconfont icon-fengxiang"></i></div>
                <div class="indicator-item forecast"><p>{{rainfall1}}</p>
                    <p class="white-txt">{{rainfall2}}</p><i class="iconfont icon-shukoubei"></i></div>
            </div>
            <div class="mgt-45">
                <borderTitle :title="title1">
                    <span><span class="main-color fs-22">当前土壤墒情：</span>{{curStatus}}</span>
                </borderTitle>
                <table class="my-table">
                    <thead>
                    <tr>
                        <th v-for="item in thead1">{{item}}</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="item in list1">
                        <td>{{item.col1}}</td>
                        <td>{{item.col2}}</td>
                        <td>{{item.col3}}</td>
                        <td>{{item.col4}}</td>
                        <td>{{item.col5}}</td>
                    </tr>
                    </tbody>
                </table>
            </div>
            <div class="mgt-45">
                <borderTitle :title="title2"></borderTitle>
                <table class="my-table2">
                    <thead>
                    <tr>
                        <th v-for="item in thead2">{{item}}</th>
                        <th>
                            <div class="last-th">虫害预防</div>
                        </th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="item in list2">
                        <td>{{item.col1}}</td>
                        <td>{{item.col2}}</td>
                        <td>{{item.col3}}</td>
                        <td>{{item.col4}}</td>
                        <td>{{item.col5}}</td>
                        <td>{{item.col6}}</td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <div class="mgt-45">
                <borderTitle :title="title3"></borderTitle>
                <div class="next-24">
                    <div class="forecast-box">
                        <div class="forecast-item" v-for="item in timeLines"><p>晴</p>
                            <p>7℃</p>
                            <p><3级</p></div>
                    </div>
                    <div class="forecast-time-box">
                        <ul class="time-horizontal clearfix" style="border-top:dashed 1px #35ece6">
                            <li v-for="item,index in timeLines.slice(0,7)">
                                <i :key="index" style="z-index:100;font-size: 12px;background-color: rgb(2, 27, 28);color: #35ece6;box-sizing:border-box;
                                position: absolute;top:-6px;left: -4px;border-radius: 50%;" class="iconfont icon-circle-o"></i>
                            </li>
                        </ul>
                        <i key="8" style="z-index:100;font-size: 12px;background-color: rgb(2, 27, 28);color: #35ece6;box-sizing:border-box;
                                position: absolute;top:-6px;right: -4px;border-radius: 50%;display: inline-block;" class="iconfont icon-circle-o"></i>
                    </div>
                    <div class="forecast-box">
                        <span v-for="item in timeLines" class="time-item">{{item}}</span>
                    </div>
                </div>
            </div>
            <div class="mgt-45">
                <borderTitle :title="title4"></borderTitle>

                <div class="next-7">
                    <router-link class="module-entrance" tag="div" to="weatherForecast">
                        <div class="module1" :style="module1Style"><i class="iconfont icon-duoyun"></i></div>
                        <span>{{moduleName1}}</span>
                    </router-link>
                    <router-link class="module-entrance" tag="div" to="moistureForecast">
                        <div class="module2" :style="module2Style"><i class="iconfont icon-shangqingzhan"></i></div>
                        <span>{{moduleName2}}</span>
                    </router-link>
                    <router-link class="module-entrance" tag="div" to="cropForecast">
                        <div class="module3" :style="module3Style"><i style="transform: rotate(45deg)"
                                                                      class="iconfont icon-qitaleinongzuowu"></i></div>
                        <span>{{moduleName3}}</span>
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import borderTitle from '../../components/borderTitle.vue'
    import moment from 'moment/src/moment'
    import {lunarMoment} from '../../assets/js/getCNDate'

    export default {
        name: "weather",
        components: {borderTitle},
        data(){
            return {
                warningLevel: '蓝色预警',
                solarTerm: '立冬',
                hint: '防治小麦黄矮病、丛矮病；防除禾本科杂草',
                weatherName: '晴',
                forecast1: '10℃',
                forecast2: '13℃-5℃',
                direction1: '东方',
                direction2: '<3级转3-4级',
                rainfall1: '降水量',
                rainfall2: '0.0mm级',
                title1: '今日墒情',
                title2: '今日农事',
                title3: '未来24小时天气预报',
                title4: '未来7天农业气象预报',
                curStatus: '适宜',
                thead1: ['土壤深度', '20CM', '40CM', '60CM', '80CM'],
                thead2: ['耕地', '播种', '定植', '施肥', '灌溉'],
                list1: [{col1: '相对湿度', col2: '86.3', col3: '81.5', col4: '74.2', col5: '67.3'}],
                list2: [{col1: '宜', col2: '不宜', col3: '-', col4: '不宜', col5: '宜', col6: '不宜'}],
//                timeLines: ['08:00', '09:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00'],
                moduleName1: '天气预报',
                moduleName2: '墒情预报',
                moduleName3: '农事预报',
                module1Style: 'border: solid 1px rgba(0, 213, 251, 0.71);',//部分手机不支持0.5px
                module2Style: 'border: solid 1px rgba(53, 236, 230, 0.65);',
                module3Style: 'border: solid 1px rgba(214, 255, 91, 0.7);'
            }
        },
        computed: {
            date(){
                return moment().format('MM月DD日')
            },
            week(){
                let weekdays = '星期日_星期一_星期二_星期三_星期四_星期五_星期六'.split('_')
                return weekdays[moment().format('d')]
            },
            cnDate(){
                return lunarMoment.getCNDate()
            },
            timeLines(){
                let arr = [];
                for (let i = 0; i < 8; i++) {
                    arr.push(moment().add(i, 'hours').format('HH') + ':00')
                }
                return arr
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/_mixin.scss";

    .weather-main{
        /*margin-top: 1.2rem;*/
      padding-top: 1.2rem;
        overflow: hidden;
        overflow-y: auto;
    }
    .weather-body {
        box-sizing: border-box;
        margin: 30px;
        /*overflow-x: hidden;*/
        padding-bottom: 90px;
        color: #ffffff;

    .main-color {
        color: #bffffb;
    }

    .top-content {
        display: flex;
        justify-content: space-around;
    }

    .flex-item {
        width: 32%;
        min-height: 119px;
        letter-spacing: 0;
        background-color: #1a3132;
        color: #ffffff;
    }

    .flex-item {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .date {
        display: inline-block;
        font-size: 24px;
        line-height: 40px;
    }

    .date p:first-child {
        color: #bffffb;
    }

    .mgl-16 {
        margin-left: 16px;
    }

    .warning-img {
        width: 90px;
        height: 80px;
    }

    .warning-txt {
        margin-left: 18px;
        font-size: 26px;
        cursor: pointer;
        line-height: 40px;

    p:first-child {
        color: #bffffb;
    }

    }

    .hint {
        font-size: 26px;
        line-height: 47px;
        color: #bffffb;

    p {
        margin-left: 14px;
    }

    p:first-child {
        margin-top: 10px;
    }

    p:last-child {
        font-size: 18px;
        line-height: 28px;
        color: #ffffff;
        margin-bottom: 16px;
    }

    }

    .weather-indicator {
        font-size: 20px;
        color: #bffffb;
        margin-top: 12px;
        display: flex;
        justify-content: space-around;

    .indicator-item {
        width: 24%;
        min-height: 95px;
        box-sizing: border-box;
        background-color: #1a3132;
        position: relative;

    .iconfont {
        position: absolute;
        right: 6px;
        bottom: 15px;
        font-size: 40px;
        opacity: 0.25;
    }

    .icon-fengxiang {
        right: -1px;
        bottom: 10px;
    }

    }

    .indicator-item:first-child span {
        display: inline-block;
        margin-left: 10px;
        font-size: 26px;
    }

    .forecast {
        padding: 20px 0 0 20px;
        line-height: 30px;

    .white-txt {
        color: #ffffff;
        margin-bottom: 10px;
    }

    }
    }

    .fs-22 {
        font-size: 22px;
    }

    .mgt-45 {
        margin-top: 45px;
    }

    .my-table, .my-table2 {
        margin-top: 36px;
        width: 100%;
        border-bottom: solid 3px #29374a;
        font-size: 22px;

    thead {
        background-image: linear-gradient(#005853, #005853), linear-gradient(#fff600, #fff600);
        background-blend-mode: normal, normal;
    }

    tr th {
        height: 80px;
        line-height: 90px;
        font-size: 24px;
    }

    tbody tr td {
        text-align: center;
        height: 80px;
        background-color: transparent;
        line-height: 90px;
    }

    tbody tr td:first-child {
        color: #bffffb;
    }

    tbody tr td:nth-child(odd) {
        background-color: rgba(26, 49, 50, 0.31);
    }

    }

    .my-table2 tbody tr td:nth-child(odd) {
        color: #38f2ec;
    }

    .my-table2 tbody tr td:nth-child(even) {
        color: #ffe328;
    }

    .last-th {
        width: 60px;
        line-height: 30px;
        display: inline-block;
        box-sizing: border-box;
        transform: translateY(16px);
    }

    .next-24 {
        width: 100%;
        position: relative;
        margin-bottom: 30px;
    }

    .forecast-box {
        display: flex;
        justify-content: space-between;
    }

    .forecast-item {
        margin: 20px 0;
        position: relative;

    p {
        font-size: 22px;
        line-height: 30px;
    }

    p:last-child {
        font-size: 20px;
    }

    }

    .forecast-time-box {
        width: 100%;
        box-sizing: border-box;
        position: relative;
    }

    .time-horizontal {
        list-style-type: none;
        width: calc(100% - 12px);
        padding: 0;
        margin: 0;
        box-sizing: border-box;
        position: relative;
        *zoom: 1;

    &:before,  &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
        overflow: hidden;
    }

    }

    .time-horizontal li {
        float: left;
        position: relative;
        padding: 0;
        width: calc(100% / 7  - 1px);/*-1px,防止模拟器IE浏览器上圆圈出现偏移*/
        padding-top: 10px;
        font-size: 22px;
        color: #bffffb;
        margin: 0;
    }

    /*.time-horizontal li:last-child {*/
        /*position: absolute;*/
        /*right: -14%;*/
    /*}*/

    .time-item {
        display: inline-block;
        margin-top: 10px;
    }

    .next-7 {
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin-top: 30px;
    }

    .module-entrance {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        text-align: center;

    span {
        font-size: 30px;
        line-height: 20px;
        display: inline-block;
        margin-top: 21px;
    }

    }

    .module-entrance:first-child:hover, .module-entrance:first-child:focus {

    .module1 {
        background-color: rgba(0, 193, 255, 0.6);
    }

    }
    .module-entrance:nth-child(2):hover, .module-entrance:nth-child(2):focus {

    .module2 {
        background-color: rgba(49, 210, 225, 0.6);
    }

    }
    .module-entrance:last-child:hover, .module-entrance:last-child:focus {

    .module3 {
        background-color: rgba(255, 240, 0, 0.6);
    }

    }

    .module1, .module2, .module3 {
        width: 106px;
        height: 106px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;

    .iconfont {
        font-size: 53px;
    }

    }

    .module1 {
        background-color: rgba(0, 193, 255, 0.4);
    }

    .module2 {
        background-color: rgba(49, 210, 225, 0.4);
    }

    .module3 {
        background-color: rgba(255, 240, 0, 0.4);
    }

    .module1 + span, .module1 .iconfont {
        color: #3de3ff;
    }

    .module2 + span, .module2 .iconfont {
        color: #38f2ec;
    }

    .module3 + span, .module3 .iconfont {
        color: #f7d912;
    }

    }
</style>


