<template>
    <div class="echart-container" id="echarts">
        温度
    </div>
</template>

<script>
    export default {
        name: "temperature",
        data(){
            return {
            }
        },
        methods:{
            initPage(){
                let myChart = this.$echarts.init(document.getElementById('echarts'));
                var datax = ['8日','9日','10日','11日','12日','13日','14日'];
                var dataser1 = [6, 6, 6, 6, 6, 6, 6];
                var dataser2 = [2.8, 1.2, 3.8, 4.4, 3.2, 1.7,3.2,1.6,4.3,3.2,4.5,4.6]
                var option = {
                    tooltip : {
                        trigger: 'axis',
                        axisPointer:{
                            lineStyle:{
                                color:'#ccc'
                            }
                        }
                    },
                    grid: {
                        left: '3%',
                        right: '3%',
                        bottom: '4%',
                        top: '2%',
                        containLabel: true
                    },
                    xAxis:  {
                        type: 'category',
                        axisPointer:{
                            show:false
                        },
                        show:true,
                        lineStyle:{
                            color:'#192d35'
                        },
                        axisTick:{
                            show: false,
                            inside: true
                        },
                        axisLabel:{
                            color:"#bffffb"
                        },
                        data:datax
                    },
                    yAxis:[
                        {
                            type: 'value',
                            axisLine:{
                                show: true,
                                lineStyle:{
                                    color:'#192d35'
                                }
                            },
                            axisTick:{
                                show: false,
                                inside: true
                            },
                            axisLabel:{
                                color:"#bffffb"
                            },
                            splitLine: {
                                show: false
                            }
                        }
                    ],
                    series: [
                        {
                            name: '数量',
                            type: 'bar',
                            itemStyle: {
                                normal: {
                                    color:'#092223',
                                }
                            },
                            barWidth: '50%',
                            label: {
                                normal: {
                                    show: false,
                                    color: ['#ff8400','#4b79ab'],
                                    fontSize: 12,
                                    position: 'top',
                                }
                            },
                            data:dataser1
                        },
                        {
                            name: '数量',
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    color:'#f79523',
                                }
                            },
                            label: {
                                normal: {
                                    show: true,
                                    fontSize: 12,
                                    position: 'top',
                                }
                            },
                            symbolSize:10,
                            data:dataser2
                        }
                    ]
                }
                myChart.setOption(option);
            }
        },
        components: {
        },
        mounted(){
            this.initPage()
        }
    }
</script>

<style lang="scss" scoped>
    .echart-container{
        height: 100%;
    }
</style>


