<template>
  <div class="weather-wapper">
    <mt-header title="天气预报">
      <router-link to="/home/weather" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="weather-tab">
      <router-link class="module-entrance" tag="div" to="/home/weatherForecast/temperature">
        <div class="module1" :style="module1Style"><i class="iconfont icon-duoyun"></i></div>
        <span>{{moduleName1}}</span>
      </router-link>
      <router-link class="module-entrance" tag="div" to="/home/weatherForecast/humidity">
        <div class="module2" :style="module2Style"><i class="iconfont icon-shangqingzhan"></i></div>
        <span>{{moduleName2}}</span>
      </router-link>
      <router-link class="module-entrance" tag="div" to="/home/weatherForecast/wind">
        <div class="module3" :style="module3Style"><i style="transform: rotate(45deg)"
                                                      class="iconfont icon-qitaleinongzuowu"></i></div>
        <span>{{moduleName3}}</span>
      </router-link>
      <router-link class="module-entrance" tag="div" to="/home/weatherForecast/precipitation">
        <div class="module4" :style="module4Style"><i style="transform: rotate(45deg)"
                                                      class="iconfont icon-qitaleinongzuowu"></i></div>
        <span>{{moduleName4}}</span>
      </router-link>
    </div>
    <div class="weather-content">
      <router-view name="weatherContent"></router-view>
    </div>
  </div>
</template>

<script>
  export default {
    name: "weather",
    data() {
      return {
        moduleName1: '温度',
        moduleName2: '湿度',
        moduleName3: '风力',
        moduleName4: '降水量',
        module1Style: 'border: solid 1px rgba(53, 236, 230, 0.65);',//部分手机不支持0.5px
        module2Style: 'border: solid 1px rgba(53, 236, 230, 0.65);',
        module3Style: 'border: solid 1px rgba(53, 236, 230, 0.65);',
        module4Style: 'border: solid 1px rgba(53, 236, 230, 0.65);',
      }
    },
    components: {}
  }
</script>

<style lang="scss" scoped>
  .weather-wapper {
    /*margin-top:1.2rem;*/
    padding-top: 1.2rem;
    color: #fff;

    .weather-tab {
      display: flex;
      justify-content: space-around;
      align-items: center;
      margin-top: 30px;
      margin-bottom: 30px;
      .router-link-exact-active, .router-link-active {
        div {
          background-color: rgba(0, 193, 255, 0.6);
        }
      }
    }
    .weather-content {
      height: calc(100vh - 3.98rem);
    }
    .module-entrance {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      text-align: center;

      span {
        font-size: 30px;
        line-height: 20px;
        display: inline-block;
        margin-top: 21px;
      }

    }

    .module-entrance:first-child:hover, .module-entrance:first-child:focus {

      .module1 {
        background-color: rgba(0, 193, 255, 0.6);
      }

    }
    .module-entrance:nth-child(2):hover, .module-entrance:nth-child(2):focus {

      .module2 {
        background-color: rgba(0, 193, 255, 0.6);
      }

    }
    .module-entrance:nth-child(3):hover, .module-entrance:nth-child(3):focus {

      .module3 {
        background-color: rgba(0, 193, 255, 0.6);
      }

    }
    .module-entrance:nth-child(4):hover, .module-entrance:nth-child(4):focus {

      .module4 {
        background-color: rgba(0, 193, 255, 0.6);
      }

    }

    .module1, .module2, .module3, .module4 {
      width: 106px;
      height: 106px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;

      .iconfont {
        font-size: 53px;
      }

    }

    .module1 {
      background-color: rgba(49, 210, 225, 0.4);
    }

    .module2 {
      background-color: rgba(49, 210, 225, 0.4);
    }

    .module3 {
      background-color: rgba(49, 210, 225, 0.4);
    }
    .module4 {
      background-color: rgba(49, 210, 225, 0.4);
    }

    .module1 + span, .module1 .iconfont {
      color: #3de3ff;
    }

    .module2 + span, .module2 .iconfont {
      color: #38f2ec;
    }

    .module3 + span, .module3 .iconfont {
      color: #3de3ff;
    }
    .module4 + span, .module4 .iconfont {
      color: #3de3ff;
    }

  }

</style>


