<template>
  <div class="article-wrap">
    <mt-header title="详情">
      <mt-button icon="back" slot="left" @click="back"></mt-button>
    </mt-header>
    <div class="scroll-box">
      <div class="article-box">
        <article>
          <h2 class="title">{{articleData.title}}</h2>
          <div class="article-info">
            <span class="year">{{articleData.date}}</span>
            <span class="author">{{articleData.author}}</span>
          </div>
          <div class="main-article">
            <div v-html="articleData.article"></div>
          </div>
        </article>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "articles",
    data() {
      return {
        articleId: '',
        articleData: {}
      }
    },
    props: {},
    computed: {},
    watch: {
      articleId(newVal) {
        this.getArticle()
      }
    },
    methods: {
      back() {
        this.$router.goBack();
      },

      getArticle() {
        this.$xhr.get('', {
          params: {
            uri: `policyid${this.articleId}`
          }
        }).then((res) => {
          this.articleData = res.data.articleInfo
          console.log(res)
        })
      },
    },

    created() {
      // this.getArticle()
    },

    beforeRouteEnter(to, from, next) {
      next((vm) => {
        vm.articleId = to.query.articleId
      })
    },
  }
</script>

<style lang="scss">
  @import "../assets/css/_mixin.scss";
  @import "../assets/css/_variable.scss";

  .article-wrap {
    .scroll-box {
      /*height: calc(100vh - 190px);*/
      height: 100%;
      margin: 90px 0 100px;
      overflow: hidden;
      overflow-y: auto;
      -webkit-overflow-scrolling: touch;
      &::-webkit-scrollbar-track-piece {
        background-color: rgba(0, 0, 0, 0);
        border-left: 1px solid rgba(0, 0, 0, 0);
      }
      &::-webkit-scrollbar {
        width: 1px;
        height: 1px;
        background-color: #000;
      }
      .article-box {
        color: #fff;
        padding: 40px 30px;
        .title {
          @include font-dpr(36px);
          font-weight: bold;
          line-height: 1.5em;
          padding: 10px 0;
        }
        .article-info {
          @include font-dpr(28px);
          line-height: 1.5em;
          padding: 10px 0;
          span:nth-child(1) {
            color: #d5faf9;
            padding-right: 60px;
          }
          span:nth-child(2) {
            color: #38f2ec;
          }
        }
        img {
          width: 100%;
          margin: 20px 0;
        }
        .main-article {
          @include font-dpr(28px);
          font-weight: normal;
          line-height: 1.5em;
          p {
            text-indent: 56px;
            padding: 10px 0;
          }
        }
      }
    }
  }
</style>


