<template>
  <div class="news-list-wrap">
    <ul>
      <li class="list-item" v-for="item,index in articleList" :key="item.articleId" @click="gotoDetail(item.articleId)">
        <div class="img-box">
          <img :src="item.imgUrl" :alt="item.title">
        </div>
        <div class="title-box">
          <h3 class="title">{{item.title}}</h3>
          <div class="view-box">
            <i class="iconfont icon-eye"></i>
            <span>{{item.view}}</span>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "news-list",
    data() {
      return {
        articleList: []
      }
    },
    props: {
      pageUrl: {
        type: String,
        default: '',
      }
    },
    watch: {
      pageUrl(newVal) {
        if (newVal) {
          this.getData()
        }
      }
    },
    created() {
      this.getData()
    },
    methods: {
      getData() {
        this.$xhr.get('', {
          params: {
            uri: this.pageUrl
          }
        }).then((res) => {
          this.articleList = res.data.articleList
        })
      },
      gotoDetail(id) {
        this.$router.push({
          path: '/home/articles',
          query: {articleId: id},
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/_mixin.scss";
  @import "../../assets/css/_variable.scss";

  .news-list-wrap {
    padding: 20px 25px;
    .list-item {
      width: 100%;
      height: 160px;
      display: flex;
      background-color: #1a3132;
      margin-bottom: 20px;
      &:nth-last-child(1) {
        margin-bottom: 0px;
      }
      .img-box {
        width: 230px;
        height: 100%;
        flex-shrink: 0;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      .title-box {
        color: #fff;
        width: 100%;
        padding: 20px 36px 20px;
        position: relative;
        .title {
          @include font-dpr(26px);
          font-weight: normal;
          line-height: 42px;
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .view-box {
          @include font-dpr(20px);
          position: absolute;
          right: 36px;
          bottom: 20px;
          display: flex;
          justify-content: flex-end;
          align-items: center;
          .iconfont {
            color: #34e5e0;
            padding-right: 10px;
          }
          span {
            color: #d5faf9;
          }
        }
      }
    }
  }
</style>


